import React from "react";
import "./App.css";

import { useWindowDimensions } from "./hooks";

const App = () => {
  const windowSize = useWindowDimensions();

  return (
    <div className="section placeholder-bkg" style={{ minHeight: windowSize.height }}>
      <div className="introduction-container">
        <span className="h3 introduction-text" style={{ animationDelay: "1250ms" }}>Hello there!</span>
        <span className="h3 introduction-text" style={{ animationDelay: "2775ms" }}>My name is <span className="placeholder-underline">Dale Christian Seen</span>.</span>
        <span className="h3 introduction-text" style={{ animationDelay: "4300ms" }}>I'm an aspiring <span className="placeholder-color">front-end developer</span>.</span>
        <div className="introduction-border-anims" />
      </div>
      <div className="introduction-container">
        <span className="body1 introduction-text" style={{ animationDelay: "5825ms" }}>...And I'm still in the middle of building this web portfolio. Please come back later!</span>
      </div>
      <span className="body1 introduction-text" style={{ animationDelay: "5825ms" }}><span className="placeholder-font-weight">Email:</span> Dale.Seen@gmail.com </span>
      <span className="body1 introduction-text" style={{ animationDelay: "5825ms" }}><span className="placeholder-font-weight">LinkedIn:</span> https://www.linkedin.com/in/dalecs</span>
    </div>
  );
};

export default App;